import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';
import DeliTakeaway from "../views/DeliTakeaway";
import DeliDeliveries from "../views/DeliDeliveries";

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/takeaway',
    name: 'Takeaway',
    component: DeliTakeaway
  },
  {
    path: '/delivery',
    name: 'Delivery',
    component: DeliDeliveries
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
