<template>
    <div class="max-w-sm mx-auto">
        <deli-header></deli-header>
        <div class="mt-6">
            <img src="../assets/counter.jpg" alt="Takeaway Meal">
            <h1 class="bg-white p-4 text-2xl">Colemans Deli Deliveries</h1>
        </div>
        <div class="bg-white p-4 my-6">
            <div class="pb-4" v-if="isLoading">Loading...</div>
            <div v-for="(group, index) of items" :key="index">
                <h3 class="text-xl font-medium">{{ group[0] }}</h3>
                <div class="mb-8">
                    <div v-for="(item, j) of group[1]" :key="j" class="flex justify-between items-center border-b border-gray-200 mb-2 py-2">
                        <span>{{ item.description }}</span>
                        <span>{{ item.price }}</span>
                    </div>
                </div>
            </div>
        </div>
        <deli-footer></deli-footer>
    </div>
</template>

<script>
  import DeliHeader from "../components/DeliHeader";
  import DeliFooter from "../components/DeliFooter";
  import * as Tabletop from "tabletop";
  import groupBy from "lodash.groupby";
  export default {
    name: "DeliDeliveries",
    components: {DeliFooter, DeliHeader},
    data() {
      return {
        items: [],
        isLoading: true
      };
    },
    async created() {
      const url = 'https://docs.google.com/spreadsheets/d/1-VUwDuPhiv5GKggX6Tfoj2M7JD4hMwYQCEmgn14YYeY/edit?usp=sharing';
      Tabletop.init({
          key: url,
          simpleSheet: false
        }
      ).then((data) => {
        const menu = [];
        let currentGroup;
        for (let row of data['Delivery'].elements) {
          if (row['Group']) {
            currentGroup = row['Group'];
          }
          if (row['Description']) {
            menu.push({
              group: currentGroup,
              description: row['Description'],
              price: row['Price']
            });
          }
        }
        this.items = Object.entries(groupBy(menu, 'group'));
        this.isLoading = false;
      });
    }
  };
</script>

